import Animation from '../../util/animation'

import calSeriesMap from '../../calculate/calSeriesMap'
import calSeriesColor from '../../calculate/calSeriesColor'
import calLegendData from '../../calculate/calLegendData'
import calAxisData from '../../calculate/calAxisData'
import calAxisRadarData from '../../calculate/calAxisRadarData'

import calChartBarData from '../../calculate/calChartBarData'
import calChartLineData from '../../calculate/calChartLineData'
import calChartPieData from '../../calculate/calChartPieData'
import calChartRadarData from '../../calculate/calChartRadarData'
import calChartScatterData from '../../calculate/calChartScatterData'
import calChartFunnelData from '../../calculate/calChartFunnelData'
import calChartCandlestickData from '../../calculate/calChartCandlestickData'
import calChartHeatmapData from '../../calculate/calChartHeatmapData'
import calChartTreemapData from '../../calculate/calChartTreemapData'
import calChartTagCloudData from '../../calculate/calChartTagCloudData'

import drawBackground from '../drawBackground'
import drawLegend from '../drawLegend'
import drawAxis from '../drawAxis'
import drawAxisRadar from '../drawAxisRadar'

import drawChartBar from './drawChartBar'
import drawChartLine from './drawChartLine'
import drawChartPie from './drawChartPie'
import drawChartRadar from './drawChartRadar'
import drawChartScatter from './drawChartScatter'
import drawChartFunnel from './drawChartFunnel'
import drawChartCandlestick from './drawChartCandlestick'
import drawChartHeatmap from './drawChartHeatmap'
import drawChartTreemap from './drawChartTreemap'
import drawChartTagCloud from './drawChartTagCloud'

export default function drawCharts() {
  // 计算数据
  calSeriesMap.call(this)
  calSeriesColor.call(this)
  calLegendData.call(this)
  // 有相同xy轴的图表，只计算一次
  if (this.seriesMap.line || this.seriesMap.bar || this.seriesMap.scatter || this.seriesMap.candlestick || this.seriesMap.heatmap) {
    calAxisData.call(this)
  }
  Object.keys(this.seriesMap).forEach(type => {
    if (this.seriesMap[type]) {
      switch (type) {
        case 'bar':
          calChartBarData.call(this)
          break
        case 'line':
          calChartLineData.call(this)
          break
        case 'pie':
          calChartPieData.call(this)
          break
        case 'radar':
          calAxisRadarData.call(this)
          calChartRadarData.call(this)
          break
        case 'scatter':
          calChartScatterData.call(this)
          break
        case 'funnel':
          calChartFunnelData.call(this)
          break
        case 'candlestick':
          calChartCandlestickData.call(this)
          break
        case 'heatmap':
          calChartHeatmapData.call(this)
          break
        case 'treemap':
          calChartTreemapData.call(this)
          break
        case 'tagCloud':
          calChartTagCloudData.call(this)
          break
      }
    }
  })

  let { animation, animationDuration, animationTiming } = this.opts
  this.animationInstance && this.animationInstance.stop()

  this.animationInstance = new Animation({
    animation,
    animationDuration,
    animationTiming,
    onProcess: process => {
      // 绘制图表
      drawBackground.call(this)
      // 有相同xy轴的图表，只绘制一次
      if (this.seriesMap.line || this.seriesMap.bar || this.seriesMap.scatter || this.seriesMap.candlestick || this.seriesMap.heatmap) {
        drawAxis.call(this)
      }

      Object.keys(this.seriesMap).forEach(type => {
        switch (type) {
          case 'bar':
            drawChartBar.call(this, process)
            break
          case 'line':
            drawChartLine.call(this, process)
            break
          case 'pie':
            drawChartPie.call(this, process)
            break
          case 'radar':
            drawAxisRadar.call(this)
            drawChartRadar.call(this, process)
            break
          case 'scatter':
            drawChartScatter.call(this, process)
            break
          case 'funnel':
            drawChartFunnel.call(this, process)
            break
          case 'candlestick':
            drawChartCandlestick.call(this, process)
            break
          case 'heatmap':
            drawChartHeatmap.call(this, process)
            break
          case 'treemap':
            drawChartTreemap.call(this, process)
            break
          case 'tagCloud':
            drawChartTagCloud.call(this, process)
            break
        }
      })

      if (process == 1) {
        drawLegend.call(this)
      }
    },
    onAnimationFinish: () => {
      this.event.trigger('renderComplete')
    },
  })
}
