import Event from './util/event'
import Config from './config'
import { replenishData, calOptions, calSeries } from './calculate/calOptions'
import drawCharts from './draw/drawCharts/drawCharts'
import cloneDeep from 'lodash.clonedeep'

class Charts {
  constructor(opts = {}) {
    this.config = Object.assign({}, Config)
    this.opts = Object.assign({}, opts)
    this.context = this.opts.element.getContext('2d')
    this.legendData = {}
    this.seriesMap = {}
    this.chartData = {}

    // 绑定事件
    this.event = new Event()
    this.event.addEventListener('renderComplete', opts.onRenderComplete)

    // 将opts的数据补充完整
    calOptions.call(this)

    // 绘制图表
    drawCharts.call(this)
  }

  updateData(updateOpts = {}) {
    Object.keys(updateOpts).forEach(key => {
      if (key == 'series') {
        this.opts.series = cloneDeep(updateOpts.series)
        calSeries.call(this)
      } else {
        replenishData(updateOpts, key, this.opts, key, true)
      }
    })

    console.log('complete updateData', this)

    drawCharts.call(this)
  }
}

export default Charts
